let minesweeperBox = {};

export function boxValue(row, col){
    if(minesweeperBox[ `${row}${col}` ] === undefined){
        //ì§€ë¢°ì°¾ê¸° ë§µì˜ ì—†ëŠ” ê°’ì´ ìˆì„ ê²½ìš° unValue
        return 'unValue';
    }else{
        return minesweeperBox[ `${row}${col}` ].isState;
    };
};

export function setRandomMines(){
    const row = 8;
    const col = 8;
    const mines = 9;

    for(let i=0; i < row; i++){
        for(let j=0; j < col; j++){
            minesweeperBox[ `${i}${j}` ] = {
                text: '',
                classList: 'box',
                isFirst: true,
                isState: 0
            }
        }
    };

    let placedMines = 0;
    let randomRow, randomCol;
    while(placedMines < mines){
        // ì´ 9ê°œì˜ ì§€ë¢°ë¥¼ ëœë¤ ìœ„ì¹˜ì— ì§€ì •
        randomRow = Math.floor(Math.random() * row);
        randomCol = Math.floor(Math.random() * col);
        if(minesweeperBox[`${randomRow}${randomCol}`].isState === 0){
            // ìˆ«ì 9ëŠ” minesì¸ì§€ ì•„ë‹Œì§€ë¥¼ íŒë³„í•˜ê¸° ìœ„í•œ ìˆ«ì, ì¦‰ ìˆ«ì 9ì¼ ë•Œ ì§€ë¢°
            minesweeperBox[`${randomRow}${randomCol}`].isState = 9;
            placedMines++;
        }
    }

    // mines ì¡°ê±´ì— ë”°ë¥¸ ìˆ«ì ë„£ê¸°
    for(let i=0; i < row; i++){
        for(let j=0; j < col; j++){
            if(minesweeperBox[`${i}${j}`].isState === 9){
                //ë§Œì•½ ì§€ë¢°ë¥¼ ê°€ì§„ ê³³ì´ë¼ë©´
                for(let ii=-1; ii<=1; ii++){
                    for(let jj=-1; jj<=1; jj++){
                        if(ii!==0 || jj!==0){
                            if(boxValue(i+ii, j+jj) !== 9 && boxValue(i+ii, j+jj) !== 'unValue'){
                                //ë§Œì•½ [0,0]ì´ë©´ [-1,-1],[-1,0],[-1,1],[0,-1],[0,0][0,1],[1,-1],[1,0],[1,1]
                                minesweeperBox[`${i+ii}${j+jj}`].isState++;
                            }
                        }
                    }
                }
            }
        }
    }

    return minesweeperBox;
};